.container {
  width: 100%; }

footer {
  height: 100%;
  margin-top: 100px;
  padding-bottom: 25px; }

.footer {
  a {
    color: white; } }

.foot-ul {
  list-style-type: none !important;
  margin-bottom: 0;
  margin-top: 20px; }


#social-media {
  margin-bottom: 0;
  li {
    display: inline;
    img {
      height: 10%;
      width: 10%; } } }

.jumbotron {
  height: 400px;
  background-image: url('../img/background_index.jpg');
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin-bottom: 0; }



.tut-wrap {
  height: 100px;
  width: 100px;
  padding: 0;
  margin: 20px auto;
  img {
    height: 100%;
    width: 100%;
    margin: 0; } }

#tutorial {
  display: inline-block; }



#courses {
  display: inline-block; }

.index_thumb {
  text-align: center;
  height: 75px;
  width: 75px;
  border: 1px solid #000000;
  border-radius: 50px; }

.index_row {
  padding-top: 25px;
  padding-bottom: 50px; }

.media {
  width: 75%;
  margin: 10px auto; }

.index_section_wrap {
  margin: 0 auto; }

.floater {
  float: right; }

.first {
  color: whitesmoke;
  background-color: #149c82; }

.third {
  color: white;
  background-color: #149c82; }
